import { IContextMenuAction } from './ContextMenu.typings';

export const contextMenuActions: IContextMenuAction = {
  'heading-1': {
    template: '# ',
    insert: str => `# ${str}`,
    startSelection: 2,
    endSelection: 11
  },
  'heading-2': {
    template: '## ',
    insert: str => `## ${str}`,
    startSelection: 3,
    endSelection: 12
  },
  'heading-3': {
    template: '### ',
    insert: str => `### ${str}`,
    startSelection: 4,
    endSelection: 13
  },
  'strong': {
    template: '****',
    insert: str => `**${str}**`,
    startSelection: 2,
    endSelection: 7
  },
  'italic': {
    template: '**',
    insert: str => `*${str}*`,
    startSelection: 1,
    endSelection: 6
  },
  'marker': {
    template: '``',
    insert: str => `\`${str}\``,
    startSelection: 1,
    endSelection: 7
  },
  'link': {
    template: '[ ](-)',
    insert: str => `[${str}](-)`,
    startSelection: 1,
    endSelection: 11
  },
  'info': {
    template: `:::info
info[]

:::`,
    insert: str => `:::info
info[]
${str}
:::`,
    startSelection: 13,
    endSelection: 22,
    multiselect: true
  },
  'alert': {
    template: `:::alert
alert[]

:::`,
    insert: str => `:::alert
alert[]
${str}
:::`,
    startSelection: 15,
    endSelection: 24,
    multiselect: true
  },
  'tip': {
    template: `:::tip
tip[]

:::`,
    insert: str => `:::tip
tip[]
${str}
:::`,
    startSelection: 11,
    endSelection: 20,
    multiselect: true
  },
  'code': {
    template: `\`\`\`js

\`\`\``,
    insert: str => `\`\`\`js
${str}
\`\`\``,
    startSelection: 6,
    endSelection: 9
  },
  'unordered-list': {
    template: `
- \n- \n- `,
    insert: str => `- ${str}`,
    startSelection: 4,
    endSelection: 10
  },
};
